import React from "react";

import {
  Text,
  CardHeader,
  Stack,
  Image,
  Box,
  Heading,
  Card,
  CardBody,
  CardFooter,
} from "grommet";

const Home = ({ dataList }) => {
  const avatarSrc = "";
  return (
    <Box direction="row" wrap={true}>
      {dataList &&
        dataList.map((e, i) => {
          const { thumbnail, title, cta } = e;
          return (
            <Box key={i} margin="small">
              <Card elevation="large" width="small">
                <Stack anchor="bottom-left">
                  {thumbnail && (
                    <CardBody height="small">
                      <Image fit="cover" src={thumbnail} a11yTitle="bridge" />
                    </CardBody>
                  )}
                  {avatarSrc && (
                    <CardHeader
                      background="#000000A0"
                      width="small"
                      align="center"
                    >
                      <Box>
                        <Text size="small">$10</Text>
                      </Box>
                    </CardHeader>
                  )}
                </Stack>
                <Box
                  fill={true}
                  justify="center"
                  alignContent="center"
                  align="center"
                >
                  <Heading level="5" textAlign="center">
                    {title}
                  </Heading>
                </Box>
                <CardFooter align="center">
                  <Box
                    fill={true}
                    justify="center"
                    alignContent="center"
                    align="center"
                  >
                    {cta}
                  </Box>
                </CardFooter>
              </Card>
            </Box>
          );
        })}
    </Box>
  );
};

export default Home;
